import { FriendList } from "./(FriendsComponents)/FriendList";
import { FriendRequests } from "./(FriendsComponents)/FriendRequest";
import { RecommendedList } from "./(FriendsComponents)/RecommendFriends";

export default function FriendsPage() {
  return (
    <div className="container mx-auto py-6 max-w-6xl">
      <h1 className="text-3xl font-bold mb-6 text-white">Friends</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Your Friends */}
        <Card className="bg-gray-900 border-gray-800">
          <CardHeader>
            <CardTitle className="text-white">Your Friends</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {loadingFriends ? (
              <div className="space-y-4">
                <p className="text-gray-400">Loading friends...</p>
                {renderFriendSkeletons()}
              </div>
            ) : friends.length === 0 ? (
              <p className="text-gray-400">You don't have any friends yet.</p>
            ) : (
              friends.map((friend) => (
                <div
                  key={friend._id}
                  className="flex items-center justify-between"
                >
                  <div className="flex items-center space-x-3">
                    <Avatar>
                      <AvatarImage
                        src={friend.profilePicture || "/placeholder.svg"}
                      />
                      <AvatarFallback>
                        {friend.username.charAt(0).toUpperCase()}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <p className="font-medium text-white">
                        {friend.username}
                      </p>
                      {friend.bio && (
                        <p className="text-sm text-gray-400">{friend.bio}</p>
                      )}
                    </div>
                  </div>
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => removeFriend(friend._id)}
                    className="text-gray-400 hover:text-red-500"
                  >
                    <UserMinus className="h-4 w-4 mr-1" />
                    <span>Remove</span>
                  </Button>
                </div>
              ))
            )}
          </CardContent>
        </Card>

        {/* Friend Requests */}
        <Card className="bg-gray-900 border-gray-800">
          <CardHeader>
            <CardTitle className="text-white">Friend Requests</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {loadingRequests ? (
              <div className="space-y-4">
                <p className="text-gray-400">Loading...</p>
                {renderFriendSkeletons().slice(0, 2)}
              </div>
            ) : friendRequests.length === 0 ? (
              <p className="text-gray-400">No pending friend requests.</p>
            ) : (
              friendRequests.map((request) => (
                <div
                  key={request._id}
                  className="flex items-center justify-between"
                >
                  <div className="flex items-center space-x-3">
                    <Avatar>
                      <AvatarImage
                        src={
                          request.sender.profilePicture || "/placeholder.svg"
                        }
                      />
                      <AvatarFallback>
                        {request.sender.username.charAt(0).toUpperCase()}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <p className="font-medium text-white">
                        {request.sender.username}
                      </p>
                      <p className="text-sm text-gray-400">
                        Wants to be your friend
                      </p>
                    </div>
                  </div>
                  <div className="flex space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() =>
                        acceptFriendRequest(request._id, request.sender._id)
                      }
                      className="bg-green-600 hover:bg-green-700 text-white border-0"
                    >
                      <Check className="h-4 w-4 mr-1" />
                      <span>Accept</span>
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => rejectFriendRequest(request._id)}
                      className="bg-red-600 hover:bg-red-700 text-white border-0"
                    >
                      <X className="h-4 w-4 mr-1" />
                      <span>Reject</span>
                    </Button>
                  </div>
                </div>
              ))
            )}
          </CardContent>
        </Card>
      </div>

      {/* Recommended Friends */}
      <Card className="mt-6 bg-gray-900 border-gray-800">
        <CardHeader>
          <CardTitle className="text-white">Recommended Friends</CardTitle>
        </CardHeader>
        <CardContent>
          {loadingRecommended ? (
            <div className="space-y-4">
              <p className="text-gray-400">Loading recommendations...</p>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {renderFriendSkeletons()}
              </div>
            </div>
          ) : recommendedFriends.length === 0 ? (
            <p className="text-gray-400">
              No recommendations available at this time.
            </p>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {recommendedFriends.map((user) => (
                <Card key={user._id} className="bg-gray-800 border-gray-700">
                  <CardContent className="p-4">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <Avatar>
                          <AvatarImage
                            src={user.profilePicture || "/placeholder.svg"}
                          />
                          <AvatarFallback>
                            {user.username.charAt(0).toUpperCase()}
                          </AvatarFallback>
                        </Avatar>
                        <div>
                          <p className="font-medium text-white">
                            {user.username}
                          </p>
                          {user.bio && (
                            <p className="text-sm text-gray-400 truncate max-w-[150px]">
                              {user.bio}
                            </p>
                          )}
                        </div>
                      </div>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => sendFriendRequest(user._id)}
                        className="bg-blue-600 hover:bg-blue-700 text-white border-0"
                      >
                        <UserPlus className="h-4 w-4 mr-1" />
                        <span>Add</span>
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
